html,
body {
  margin: 0;
  background-color: #f2f7ff;
  color: black;
}

hr {
  background: #f2f7ff;
  padding: 0;
  margin: 0;
  border: 1px solid black;
}

.white1 {
  background-color: #f2f7ff;
  margin-top: 0;
}
.card {
  width: -webkit-max-content;
  width: max-content;
  background: #fff;
  /* margin: 2vw; */
  width: 20vw;
  padding: 0.5rem;
  cursor: pointer;
}

@media only screen and (max-width: 600px) {
  .card {
    width: 42vw;
  }
}
.card__container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 1rem;
    gap: 1rem;
    grid-column-gap: 1rem;
    -webkit-column-gap: 1rem;
            column-gap: 1rem;
    margin: 0 4rem;
}

@media only screen and (max-width: 600px) {
    .card__container {
        grid-template-columns: repeat(2, 1fr);
        margin: 0 1rem;
    }
  }
.filter__container {
    width: 85vw;
    margin: 0 5vw;
    display: flex;
    flex-direction: row;
    grid-gap: 2vw;
    gap: 2vw;
    margin-right: 7vw;
    margin-bottom: 2rem;
    margin-top: 3rem;
}

.filter__item {
    background: #fff;
    padding: 0.5rem 0.8rem;
    border-radius: 2rem;
    border: 1px solid transparent;
    width: -webkit-max-content;
    width: max-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
}

.search {
    margin-left: auto;
}

.filter__item:hover {
    cursor: pointer;
}

.filactive {
    color: #1600e5;
    background: #E3E1F4;
    border-color: #d6d3f8;
}


@media only screen and (max-width: 600px) {
    .filter__container {
        overflow-x: scroll;
    }
  }
.header {
    text-align: center;
}
.logo {
  height: 10vh;
  margin-right: 5vw;
}

.navbar {
  background: #f2f7ff;
  padding: 0;
  height: 10vh;
}

.link {
    margin: 1rem;
    color: black;
    text-decoration: none;
    font-weight: 600;
}

.link:hover {
    color: blue;
    cursor: pointer;
}

.active {
    color: blue;
}

.refresh {
    color: blue;
    cursor: pointer;
    border: 1px solid blue;
    border-radius: 50%;
    width: -webkit-max-content;
    width: max-content;
    padding: 0.3rem;
}

.offer__container {
    width: 90vw;
    background: #fff;
    margin-top: 0;
    padding: 0.6rem;
    border-radius: 0.5rem;
    margin-top: 2.5rem;
    display: grid;
    grid-template-columns: 1fr auto 2fr;
    margin-left: 5vw;
}

.offer__upgrade {
    color: #fff;
    background: blue;
    width: -webkit-max-content;
    width: max-content;
    border-radius: 0.5rem;
    padding: 0.7rem;
    height: -webkit-max-content;
    height: max-content;
}

.offer__content {
    margin-top: 0.5rem;
}

.out {
    margin-left: 5rem;
}

.status{
    margin-top: 0;
    margin-left: 4rem;
    background: gray;
    height: 0.3rem;
}

.progress__content {
    background: white;
    margin-left: 4rem;
    display: flex;
    flex-direction: row;
}

@media only screen and (max-width: 600px) {
    .offer__upgrade {
        margin-right: 0.3rem;
        margin-top: 1rem;
    }
    .progress1{
        display: none;
    }
  }
